import './App.css';
import { Navbar, Container, Nav, Row, Col } from 'react-bootstrap';
import data from './data.js';
import React, { Suspense, useState, createContext, useEffect } from 'react';
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom';
import axios from 'axios';
import { useQuery } from "react-query"
// import 

// import Cart from './routes/Cart.js'
// import Detail from './routes/Detail.js';

const Detail = React.lazy(() => import('./routes/Detail.js'))
const Cart = React.lazy(() => import('./routes/Cart.js'))

// lazy import 

export let Context1 = createContext();

// context 


function App() {

  let [shoes, setShoes] = useState(data);
  let navigate = useNavigate();
  let [clicknum, setClicknum] = useState(2);
  let [btncatch, setBtncatch] = useState(false);
  let [loading, setLoading] = useState(false);
  let [] = useState([10, 11, 12]);

  useEffect(() => {
    if (JSON.parse(localStorage.getItem('watched')) == null) {
      localStorage.setItem('watched', JSON.stringify([]))
    }
  }, [])


  let result = useQuery('', () =>
    axios.get('https://codingapple1.github.io/userdata.json')
      .then((a) => { console.log(''); return a.data }),
    { staleTime: 2000 }
  )

  return (
    <div className="App">
      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand onClick={() => { navigate('/') }}>Jaem</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={() => { navigate('/') }}>Home</Nav.Link>
            <Nav.Link onClick={() => { navigate('/about') }}>Features</Nav.Link>
            <Nav.Link onClick={() => { navigate('/detail/0') }}>Detail</Nav.Link>
            <Nav.Link onClick={() => { navigate('/cart') }}>Cart</Nav.Link>
          </Nav>
          <Navbar.Brand className='ms-auto'>
            {result.isLoading && ''}
            {result.error && ''}
            {result.data && result.data.name}
          </Navbar.Brand>
        </Container>
      </Navbar>
      <div className='main-big'></div>

      <Suspense fallback={<div>Loading...</div>}>
        <Routes>
          <Route path='/' element={
            <>
              <Container>
                <Row>
                  {
                    shoes.map(function (a, i) {
                      return (
                        <Card key={i} shoes={shoes[i]} i={i}></Card>
                      )
                    })
                  }
                </Row>
              </Container>
              {
                loading == true ? <div>   . . .</div> : null
              }
              <button onClick={() => {
                axios.get('https://codingapple1.github.io/shop/data' + clicknum + '.json').then((result) => {
                  setLoading(true);
                  let new_shoes = [...shoes, ...result.data];
                  setShoes(new_shoes);
                  setClicknum(clicknum + 1);
                  setLoading(false);
                })
                  .catch(() => {
                    setBtncatch(true);
                    setLoading(false);
                  })
              }}></button>


              {
                btncatch == true ? <div>  .</div> : null
              }

            </>
          } />
          <Route path='/detail/:id' element={
            <Context1.Provider value={{ , shoes }}>
              <Detail shoes={shoes} />
            </Context1.Provider>
          } />
          <Route path='/about' element={<About />}>
            <Route path='member' element={<div></div>} />
            <Route path='location' element={<div></div>} />
          </Route>
          <Route path='/event' element={<Eventpage />}>
            <Route path='one' element={<div>   </div>} />
            <Route path='two' element={<div> </div>} />
          </Route>
          <Route path='/all' element={<div>404 page error</div>} />
          <Route path='/cart' element={<Cart />}></Route>
        </Routes>
      </Suspense>


    </div >
  );
}

function Card(props) {
  let navigate = useNavigate()
  let a = props.a
  let j = props.i + 1
  return (
    <Col md={4} onClick={() => {
      navigate('/detail/' + props.i)
    }}>
      <img src={'https://codingapple1.github.io/shop/shoes' + j + '.jpg'} width="80%"></img>
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content}</p>
      <p>{props.shoes.price} </p>
    </Col>
  )
}

function About() {
  return (
    <div>
      <h4></h4>
      <Outlet></Outlet>
    </div>
  )
}

function Eventpage() {
  return (
    <div>
      <h1> </h1>
      <Outlet></Outlet>
    </div>
  )
}

export default App;
